@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Epilogue&display=swap');

*,
html {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
  box-sizing: border-box;
}
body{
  background-color: aliceblue;
  height: 100vh;
}
.main{
  height: 200vh;
}
.icon{
  background-image: url(https://pbs.twimg.com/media/Fs7PQNFaIAIKsGR?format=webp);
  width: 50px;
  height: 50px;
  border-radius: 5px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.Main-icon{
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.Main-icon > div{
  color: gray ;
  font-weight: 600;
}
.Nav-main{
  position: fixed;
  width: 100%;
  background-color: rgb(230, 230, 230, 0.6);
}
.Nav-kota{
  text-decoration: none;
  list-style: none;
  color: gray;
  padding: 8px;
  padding-top: 3px;
  padding-bottom: 1px;
  border-bottom:  rgb(193, 192, 192, 0) solid 3px;
}
.on-kota{
  border-bottom:  rgb(193, 192, 192, 0.6) solid 3px;
}
.Nav-kota:hover{
  background-color: rgb(193, 192, 192, 0.2);
  transition: ease 0.3s;
  border-radius: 5px 5px 0 0 ;
}
.Nav-bg{
  height: auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(193, 192, 192);
  backdrop-filter: blur(20px);
}
.Nav-v{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  cursor: pointer ;
}
